@import url(https://fonts.googleapis.com/css?family=Lato:700&display=swap);
.sort-visualizer{position:absolute;display:flex;justify-content:center;align-items:flex-end;bottom:0px;width:100%;z-index:-100}.sort-visualizer div{width:10px;margin:0 1.5px}.dropdown-menu{background-color:#017aff;color:#fff}.dropdown-item{color:#fff;text-align:center}.slider{margin-top:20px;color:#fff;width:200px}.slider p{position:absolute;top:-10px}

.App{text-align:center;margin:20px 20px 0px 20px}.App-logo{height:40vmin}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#09d3ac}html,body{background-color:#2f2f2f;font-family:'Lato', sans-serif}

